/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { useState, RawHTML } from '@wordpress/element';
import { useBlockProps } from '@wordpress/block-editor';
import { ResizableBox, Button } from '@wordpress/components';
import { Icon, globe, mobile, tablet, desktop, arrowUp, arrowDown, edit } from '@wordpress/icons';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import variations from './variations';
import { blockIcon, cssIcon, jsIcon } from './icon';
import Editor from './components/Editor';
import { DynamicStyles } from '@components';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * 
 */
const minHeight = 100;
const maxHeight = 1000;

const DEVICES = [
	{
		name: 'all',
		icon: globe,
		arrow: null,
		desc: '',
	},
	{
		name: 'pc',
		icon: desktop,
		arrow: arrowUp,
		desc: '@media (width >= 1000px)',
	},
	{
		name: 'sp',
		icon: tablet,
		arrow: arrowDown,
		desc: '@media (width < 1000px)',
	},
	{
		name: 'mb',
		icon: mobile,
		arrow: arrowDown,
		desc: '@media (width < 600px)',
	},
	{
		name: 'editor',
		icon: edit,
		arrow: null,
		desc: __('CSS for Editor', 'arkhe-blocks'),
	},
];

/**
 * style
 */
function joinCustomCSS(code) {
	let css = '';
	if (code?.all) {
		css += code.all;
	}
	if (code?.editor) {
		css += code.editor;
	}
	if (code?.pc) {
		css += `
			@media (min-width: 1000px) {
				${code.pc}
			}`;
	}
	if (code?.sp) {
		css += `
			@media not all and (min-width: 1000px) {
				${code.sp}
			}`;
	}
	if (code?.mb) {
		css += `
			@media not all and (min-width: 600px) {
				${code.mb}
			}`;
	}

	return css;
}
/**
 * Register Block
 */
const blockName = 'ark-block-custom-code';
registerBlockType(metadata.name, {
	title: __('Custom Code', 'arkhe-blocks'),
	description: __('Create advanced code editor', 'arkhe-blocks'),
	icon: blockIcon,
	variations,
	edit: ({ attributes, setAttributes, isSelected, toggleSelection }) => {
		// content: css / code: js
		const { variation, content, code, height } = attributes;

		const [deviceMode, setDeviceMode] = useState('all');
		const [deviceQuery, setDeviceQuery] = useState('');

		const isCSS = 'css' === variation;
		const isJS = 'javascript' === variation;

		let variationIcon = blockIcon;
		let variationText = 'Code';

		if (isCSS) {
			variationIcon = cssIcon;
			variationText = 'CSS';
		} else if (isJS) {
			variationIcon = jsIcon;
			variationText = 'JS';
			if (!IS_PRO) variationText += ' (only PRO)';
		}

		const blockProps = useBlockProps({ className: `${blockName} -${variation}` });

		const deviceBtns = window.arkbSettings?.isArkhe && isCSS && (
			<>
				<div className='__now_query'>{deviceQuery}</div>
				<fieldset className='__deviceBtns'>
					{DEVICES.map((device) => {
						const hasCode = !!(content[device.name] || '');
						return (
							<Button
								isPrimary={deviceMode === device.name}
								data-has-code={hasCode}
								onClick={() => {
									setDeviceMode(device.name);
									setDeviceQuery(device.desc);
								}}
								key={`devicemode_${device.name}`}
							>
								<Icon icon={device.icon} size='1em' className='__deviceIcon' />
								{device.arrow && (
									<Icon icon={device.arrow} size='1em' className='__arrowIcon' />
								)}
							</Button>
						);
					})}
				</fieldset>
			</>
		);

		let setCode;
		if (isCSS) {
			setCode = (newCode) => {
				setAttributes({ content: { ...content, [deviceMode]: newCode } });
			};
		} else {
			setCode = (newCode) => {
				setAttributes({ code: newCode });
			};
		}

		let currentCode;
		if (isCSS) {
			currentCode = content[deviceMode] || '';
		} else {
			currentCode = code || '';
		}

		const dynamicStyles = isCSS && joinCustomCSS(content);

		let availavle = true;
		if (!IS_PRO) {
			if (isJS || (isCSS && 'editor' === deviceMode)) {
				availavle = false;
			}
		}

		return (
			<div {...blockProps}>
				{dynamicStyles && <DynamicStyles style={dynamicStyles} />}
				<div className={`${blockName}__label`}>
					<span className='__title'>
						<Icon icon={variationIcon} className='__icon' />
						<span>{variationText}</span>
					</span>
					{deviceBtns}
				</div>
				<ResizableBox
					size={{ height }}
					minHeight={minHeight}
					enable={{
						top: false,
						right: false,
						bottom: true,
						left: false,
						topRight: false,
						bottomRight: false,
						bottomLeft: false,
						topLeft: false,
					}}
					onResizeStart={toggleSelection}
					onResizeStop={(event, direction, elt, delta) => {
						// 
						const newHeight = Math.min(
							Math.max(parseInt(height + delta.height, 10), minHeight),
							maxHeight
						);
						setAttributes({ height: newHeight });
					}}
					showHandle={isSelected}
				>
					<Editor
						blockName={blockName}
						lang={variation}
						value={currentCode}
						onChange={setCode}
						deviceMode={deviceMode}
						isSelected={isSelected}
						availavle={availavle}
					/>
				</ResizableBox>
			</div>
		);
	},

	save: ({ attributes }) => {
		const { variation, code } = attributes;
		if ('css' === variation) {
			return null;
		}
		return <RawHTML>{code}</RawHTML>;
	},
});
