/**
 * @others dependencies
 */
import webfontloader from 'webfontloader';
import { emmetCSS } from 'emmet-monaco-es';

/**
 * @WordPress dependencies
 */
import { useEffect, useState, useRef } from '@wordpress/element';
import { Spinner } from '@wordpress/components';
import { useResizeObserver } from '@wordpress/compose';

/**
 * @Internal dependencies
 */
import loader from '../loader';
import themes from '../themes';
import { ArkbProOnly } from '@components';

/**
 * Monaco Editor
 */
const DEFAULT_OPTIONS = {
	fontFamily: 'Fira Code',
	fontSize: 14,
	lineHeight: 24,
	wordWrap: 'on', // 
	hover: false, // 
	minimap: {
		// 
		enabled: false,
	},
	padding: {
		top: 10,
	},
};

export default ({ blockName, value, lang, deviceMode, onChange, isSelected, availavle = true }) => {
	const currentCode = value || '';

	if (!availavle) {
		return (
			<>
				<ArkbProOnly type='edit'>
					<div className={`${blockName}__container`} style={{ height: '100%' }}>
						<pre className={`${blockName}__preview`}>
							<code>{value}</code>
						</pre>
					</div>
				</ArkbProOnly>
			</>
		);
	}

	// 
	const containerRef = useRef(null); // Monaco Editor
	const monacoRef = useRef(null); // Monaco
	const editorRef = useRef(null); // Monaco Editor

	// Observer
	const [resizeListener, size] = useResizeObserver();

	// Monaco
	const [isMonacoMounted, setIsMonacoMounted] = useState(false);

	// 
	const [isEditorReady, setIsEditorReady] = useState(false);

	// onDidChangeModelContent 
	const [onChangeDisposable, setOnChangeDisposable] = useState(null);

	// 
	const setCode = () => {
		const newContent = editorRef.current.getValue();
		onChange(newContent);
	};

	// Monaco Editor
	useEffect(() => {
		const { defaultView } = containerRef.current.ownerDocument;
		const cancelable = loader.init(defaultView);

		cancelable.then((monaco) => {
			if (monaco) {
				// Monaco
				monacoRef.current = monaco;
				setIsMonacoMounted(true);
			}
		});

		// Monaco
		const interval = setInterval(() => {
			const { monaco } = defaultView;
			if (monaco?.editor && !isMonacoMounted && !isEditorReady) {
				// Monaco
				monacoRef.current = monaco;
				clearInterval(interval);
				setIsMonacoMounted(true);
			}
		}, 500);

		// 
		return () => {
			if (editorRef) {
				// 
				editorRef.current?.getModel()?.dispose();
				editorRef.current?.dispose();
			} else {
				// Monaco Editor
				cancelable.cancel();
			}
			clearInterval(interval);
		};
	}, []);

	// 
	useEffect(() => {
		// 
		if (!isSelected || !isMonacoMounted || isEditorReady) return;

		const { ownerDocument } = containerRef.current;
		const { defaultView } = ownerDocument;
		// const { customCodeOptions } = window.arkbSettings;

		const CURRENT_MONACO__EDITOR = monacoRef.current.editor;

		// 
		// const options = customCodeOptions?.options || {};

		// editorRef.current 
		editorRef.current = CURRENT_MONACO__EDITOR.create(containerRef.current, {
			...DEFAULT_OPTIONS,
			// ...options,
		});

		const CURRENT_EDITOR = editorRef.current;

		// 
		CURRENT_MONACO__EDITOR.setModelLanguage(CURRENT_EDITOR?.getModel(), lang);

		// 
		const theme = 'vs-dark'; //customCodeOptions.settings.theme || 'vs-dark';

		if ('vs-dark' === theme) {
			// vs-darksetTheme
			CURRENT_MONACO__EDITOR.setTheme(theme);
		} else {
			// vs-dark
			const targetTheme = themes.find((data) => theme === data.value);
			if (targetTheme) {
				CURRENT_MONACO__EDITOR.defineTheme(targetTheme.value, targetTheme.data);
				CURRENT_MONACO__EDITOR.setTheme(targetTheme.value);
			} else {
				CURRENT_MONACO__EDITOR.setTheme('vs-dark');
			}
		}

		// attributes
		const disposable = CURRENT_EDITOR?.onDidChangeModelContent(() => {
			setCode();
		});
		setOnChangeDisposable(disposable);

		// Web
		const { fontFamilies } = window.arkbBlockVars;
		if (!fontFamilies) {
			// eslint-disable-next-line no-console
			console.log('not found fontFamilies !');
			return;
		}

		const fontFamily = 'Fira Code'; // customCodeOptions.options.fontFamily || 'Fira Code';
		const font = fontFamilies.find((data) => fontFamily === data.val);

		if (!font) return;

		const webfontConfig = {
			timeout: 5000,
			custom: {
				families: [font.val],
			},
			// Web
			active: () => {
				CURRENT_MONACO__EDITOR.remeasureFonts();
			},
			inactive: () => {
				CURRENT_MONACO__EDITOR.remeasureFonts();
			},
			context: defaultView,
		};

		const link = ownerDocument.querySelector(`link[href="${font.stylesheet}"]`);
		if (!link) {
			webfontConfig.custom.urls = [font.stylesheet];
		}

		webfontloader.load(webfontConfig);

		// Emmet(CSS)
		if (lang === 'css' && !defaultView.arkbEmmetIsInitialized) {
			defaultView.arkbEmmetIsInitialized = true;
			emmetCSS(monacoRef.current);
		}

		// Ctrl(Cmd)+X
		CURRENT_EDITOR.addCommand(
			// eslint-disable-next-line no-bitwise
			monacoRef.current.KeyMod.CtrlCmd | monacoRef.current.KeyCode.KEY_X,
			() => {
				const selection = CURRENT_EDITOR.getSelection();
				const lineNumber = CURRENT_EDITOR.getPosition().lineNumber;
				// 
				const isEmptySelection =
					selection.startLineNumber === selection.endLineNumber &&
					selection.startColumn === selection.endColumn;

				// 
				if (isEmptySelection) {
					CURRENT_EDITOR.setSelection(
						new monacoRef.current.Selection(lineNumber, 1, lineNumber + 1, 1)
					);
					ownerDocument.execCommand('cut');
				}

				ownerDocument.execCommand('cut');
			}
		);

		// Ctrl(Cmd)+C
		CURRENT_EDITOR.addCommand(
			// eslint-disable-next-line no-bitwise
			monacoRef.current.KeyMod.CtrlCmd | monacoRef.current.KeyCode.KEY_C,
			() => {
				const selection = CURRENT_EDITOR.getSelection();
				const lineNumber = CURRENT_EDITOR.getPosition().lineNumber;

				// 
				const isEmptySelection =
					selection.startLineNumber === selection.endLineNumber &&
					selection.startColumn === selection.endColumn;

				if (isEmptySelection) {
					// 
					CURRENT_EDITOR.setSelection(
						new monacoRef.current.Selection(lineNumber, 1, lineNumber + 1, 1)
					);
					ownerDocument.execCommand('copy');
				}
				ownerDocument.execCommand('copy');
			}
		);

		setIsEditorReady(true);

		// 
		CURRENT_EDITOR.getModel().setValue(currentCode);
	}, [isSelected, isMonacoMounted, isEditorReady]);

	// attributes
	//  setValue() executeEdits() 
	// useEffect(() => {
	// 	if (!isEditorReady) return;
	// 	if (currentCode !== editorRef.current.getValue()) {
	// 		editorRef.current.executeEdits('', [
	// 			{
	// 				range: editorRef.current?.getModel().getFullModelRange(),
	// 				text: currentCode,
	// 				forceMoveMarkers: true,
	// 			},
	// 		]);
	// 		editorRef.current.pushUndoStop();
	// 	}
	// }, [isEditorReady]);

	// 
	useEffect(() => {
		if (!isEditorReady) return;

		// 
		onChangeDisposable?.dispose(); // 
		const disposable = editorRef.current.onDidChangeModelContent(() => {
			setCode();
		});
		setOnChangeDisposable(disposable);

		// 
		editorRef.current.getModel().setValue(currentCode);
	}, [deviceMode]);

	// 
	useEffect(() => {
		const { width, height } = size;
		if (isEditorReady && width && height) {
			editorRef.current.layout();
		}
	}, [size, isEditorReady]);

	return (
		<>
			{isSelected && availavle && !isEditorReady && (
				<div className={`${blockName}__loadingLayer`}>
					<Spinner />
				</div>
			)}
			{/* {isSelected && !availavle && <ArkbProOnly></ArkbProOnly>} */}
			{resizeListener}
			<div
				ref={containerRef}
				className={`${blockName}__container`}
				style={{ height: '100%' }}
			>
				{!isEditorReady && (
					<pre className={`${blockName}__preview`}>
						<code>{value}</code>
					</pre>
				)}
			</div>
		</>
	);
};
