import { __ } from '@wordpress/i18n';
import { cssIcon, jsIcon } from './icon';

const IS_PRO = !!window.arkbSettings?.pfkey || false;

/**
 * 
 */
const variations = [
	{
		name: 'css',
		title: __('Custom CSS', 'arkhe-blocks') + ' ()',
		attributes: { variation: 'css' },
		keywords: ['code', 'css', 'style'],
		icon: cssIcon,
		scope: ['inserter'],
		isDefault: true,
		isActive: (blockAttributes, variationAttributes) => {
			return blockAttributes.variation === variationAttributes.variation;
		},
	},
];

if (IS_PRO) {
	variations.push({
		name: 'js',
		title: __('Custom JS', 'arkhe-blocks') + ' ()',
		attributes: { variation: 'javascript' },
		keywords: ['code', 'js', 'javascript'],
		icon: jsIcon,
		scope: ['inserter'],
		isActive: (blockAttributes, variationAttributes) => {
			return blockAttributes.variation === variationAttributes.variation;
		},
	});
}
export default variations;
