/**
 * Monaco Editor 
 * see: https://github.com/suren-atoyan/monaco-loader
 */

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n';

/**
 * Monaco Editor
 */
const loaderConfig = {
	paths: {
		vs: 'https://cdn.jsdelivr.net/npm/monaco-editor@0.28.1/min/vs',
	},
};

// Promise
// https://reactjs.org/blog/2015/12/16/ismounted-antipattern.html
function makeCancelable(promise) {
	let hasCanceled_ = false;
	const canceledMessage = {
		type: 'cancelation',
		msg: __('Operation is cancelled.', 'arkhe-blocks'),
	};
	const wrappedPromise = new Promise((resolve, reject) => {
		promise.then((val) => (hasCanceled_ ? reject(canceledMessage) : resolve(val)));
		promise.catch(reject);
	});
	return (wrappedPromise.cancel = () => (hasCanceled_ = true)), wrappedPromise;
}

// Monaco Editor
function init(targetWindow = window) {
	// Monaco EditorPromise
	const promise = new Promise((resolve, reject) => {
		// 
		const timeout = setTimeout(() => {
			return reject({
				type: 'timeout',
				msg: __('Editor loading timed out.', 'arkhe-blocks'),
			});
		}, 5 * 10000);

		// windowPromise
		if (targetWindow.arkbMonacoIsInitialized) {
			clearTimeout(timeout);
			return resolve();
		}

		// windowPromise
		if (targetWindow?.monaco?.editor) {
			clearTimeout(timeout);
			return resolve(targetWindow.monaco);
		}

		// Monaco Editor
		const script = targetWindow.document.createElement('script');
		script.src = `${loaderConfig.paths.vs}/loader.js`;

		script.onload = () => {
			const require = targetWindow.require;
			require.config(loaderConfig);
			require(['vs/editor/editor.main'], (monaco) => {
				clearTimeout(timeout);
				return resolve(monaco);
			});
		};

		// Monaco Editor
		script.onerror = () => {
			clearTimeout(timeout);
			return reject({
				type: 'scripterror',
				msg: __('Failed to load the editor script.', 'arkhe-blocks'),
			});
		};

		targetWindow.document.body.appendChild(script);
		targetWindow.arkbMonacoIsInitialized = true;
	});

	// Promise
	return makeCancelable(promise);
}

export default { init };
